@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
*
{
    margin: 0;
    padding:0;
}

#game
{
    text-align: center;
    background: url(/word_scramble/static/media/pexels-anni-roenkae-2457284.5d50adac.jpg);
    background-size: cover;
    overflow: hidden;
}

#words
{
    margin-top: 50px;
    width:300px;
    margin-left: auto;
    margin-right: auto;
    border:none;
    border-bottom: 5px black solid;
    padding-bottom: 12px;
    outline:none;
    font-size: 35px;
    font-weight: bold;
    height:25px;
    color: rgb(255, 103, 197);
    text-shadow: 1px 2px 3px  black;
    border-radius: 50px;
    font-family: 'Roboto Condensed', sans-serif;}

#ts
{
    font-size: 30px;
    font-weight: bold;
    display:flex;
    justify-content: space-evenly;
}

#timer 
{
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
    

}
#score
{
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
    border:3px black solid;
    border-radius: 5px;
    background-color: white;
    
}

#sec 
{
    font-size: 26px;
    margin-left: 20px;
    border-radius: 5px;
    

}

#scr
{
    font-size: 28px;
    margin-top: -2px;
    margin-left: 10px;
   
}

#level
{
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
    border:3px black solid;
    border-radius: 5px;
    background-color: white;
}

#lvl
{
    margin-left: 7px;
    margin-top: -4px;
}

#enter
{
    width:130px;
    height: 40px;
    border:2px black solid;
    border-radius: 12px;
    background-color: rgb(90, 214, 90);
    outline:none;
    font-weight: bold;
    box-shadow: 1px 6px 4px rgb(43, 42, 42);

}
#backspace
{
    width:130px;
    height: 40px;
    border:2px black solid;
    border-radius: 12px;
    background-color: rgb(231, 112, 211);
    outline:none;
    box-shadow: 1px 6px 4px rgb(43, 42, 42);

    font-weight: bold;
}
#skip
{
    width:130px;
    height: 40px;
    border:2px black solid;
    border-radius: 12px;
    background-color: rgb(243, 116, 66);
    outline:none;
    box-shadow: 1px 6px 4px rgb(43, 42, 42);
    display:inline;
    font-weight: bold;
}
#jumble
{
    width:130px;
    height: 40px;
    border:2px black solid;
    border-radius: 12px;
    background-color: rgb(72, 196, 218);
    outline:none;
    font-weight: bold;
    box-shadow: 1px 6px 4px rgb(43, 42, 42);
}

#end
{
    width:130px;
    height: 40px;
    border:2px black solid;
    border-radius: 12px;
    background-color: rgb(245, 86, 74);
    font-weight: bold;
    outline:none;
    box-shadow: 1px 6px 4px rgb(43, 42, 42);


}

#jumble:hover
{
    background-color: rgb(16, 219, 255);
}

#enter:hover
{
    background-color: rgb(65, 241, 65);
}

#skip:hover
{
    background-color: rgb(247, 96, 36);   
}

#backspace:hover
{
    background-color: rgb(255, 107, 230);
}

#end:hover
{
    background-color: rgb(245, 63, 50);
}

#three,#four,#five,#six
{
    height:400px;
    width: 300px;
    border:3px solid black;
    border-radius: 10px;
    margin-bottom: 40px;
    box-shadow: 2px 2px 5px black;
    background-color: rgba(255, 248, 220, 0.596);
    font-family: 'Roboto Condensed', sans-serif;
    
}

#rr
{
    height:340px;
    overflow-y: scroll;
}

#wordBoxes
{
    display: flex;
    margin-top: 40px;
    justify-content: space-evenly;
}

#error
{
    color:rgb(255, 230, 0);
    font-weight: bold;
    font-size: 35px;
    text-shadow: 3px 1px 4px black;
    
}

hr
{
    height: 3px;
    background-color: black;
    border: none;

}

#head
{
    font-size: 25px;
    padding-bottom: 5px;
    padding-top: 5px;
    text-shadow: 1px 1px 1px black;

}

#wg
{
    margin-top: 40px;
    font-size: 50px;
    font-family: 'Bungee', cursive;
    text-shadow: 5px 5px black;

    margin-bottom: 40px;
    color:white;
}


*
{
    margin:0;
    padding:0;
}

#results
{
    margin-left: auto;
    margin-right: auto;
    height: 600px;
    width:750px;
    border:6px black solid;
    border-radius: 20px;
    margin-top: 40px;
    text-align: center;
    box-shadow: 7px 10px 9px rgba(80, 79, 79, 0.658);
    background-color: rgba(190, 190, 186, 0.356);
}

#gameOver
{
    text-align: center;
    background: url(/word_scramble/static/media/pexels-dids-1424495.a76947d8.jpg);
    background-size: cover;
    overflow: hidden;
    height: 100vh;
}

#go
{
    font-size: 40px;
    font-family: 'Audiowide', cursive;
    margin-top: 30px;
    color:rgb(250, 37, 72);
    text-shadow: 1px 1px 3px black;
}

#scre
{
    margin-top: 20px;
    font-size: 30px;
    text-shadow: 1px 1px 3px rgb(51, 50, 50);
}
#wrdscr
{
    display: flex;
    justify-content: space-evenly;
}


#crtwrds,#msdwrds
{
    width: 250px;
    height: 420px;
    margin-top:40px ;
    border: 2px solid black ;
    border-radius: 12px ;
    background-color: #f0b5a5c0;
    margin-left: 30px;
    stroke-opacity: initial;
}


::-webkit-scrollbar {
    display: none;
  }

  #hr
  {
      overflow-y: scroll;
      height:350px;
      text-shadow: 0.5px 0.5px 3px rgba(255, 255, 255, 0.616);
  }

  #he
  {
      padding-top: 7px;
      padding-bottom: 7px;
      text-shadow: 1px 0.5px rgba(8, 8, 8, 0.616);


  }

  #mm
  {
      width:130px;
      border:2px solid black;
      border-radius: 8px;
      height: 40px;
      background-color: rgb(46, 255, 157);
      box-shadow: 2px 2px 5px black ;
      outline:none;
      font-weight: bold;
      font-size: 17px;
    }

  #cn
  {
      
    border:2px solid black;
    border-radius: 8px;
      width: 120px;
      height: 40px;
      margin-left: 15px;
      background-color: rgb(255, 199, 46);
      box-shadow: 2px 2px 5px black ;
      font-weight: bold;
      font-size: 17px;
      outline: none;
  }

  #mm:hover
  {
      transform: scale(1.05);
  }
  #cn:hover
  {
      transform: scale(1.05);
  }
  
